* {
  box-sizing: border-box;
}

body {
  background-color: #f1f1f1;
  font-family: "Nunito", sans-serif;
  margin: 0;
  height: 100%;
  display: flex;
  justify-content: center;
  height: 100vh;
  align-items: center;
}

.is-hidden {
  display: none;
}

.game__footer {
  position: absolute;
  width: 105px;
  bottom: 10px;
  left: 50%;
  margin-left: -60px;
  font-size: 0.6em;
  color: #fff;
  text-align: center;
}
.game__footer a {
  display: inline-block;
  background: #000;
  color: #fff;
  text-decoration: none;
}

.game-intro {
  color: #fff;
}
.game-intro__heading {
  z-index: 2;
  position: relative;
  top: 0;
  left: 0;
  height: 100px;
  line-height: 100px;
}
.game-intro__title {
  text-align: center;
  text-transform: uppercase;
  font-size: 2em;
}
.game-intro__wrapper {
  position: absolute;
  top: 0;
  width: 50%;
  height: 100%;
  cursor: pointer;
}
.game-intro__wrapper:first {
  left: 0;
}
.game-intro__wrapper:last-child {
  left: 50%;
}
.game-intro__wrapper--separator {
  z-index: 2;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  margin-top: -25px;
  margin-left: -25px;
  line-height: 50px;
  background: #fff;
  border-radius: 50%;
  color: #333;
  font-size: 1em;
  text-align: center;
}
.game-intro__wrapper--single {
  background-color: #4a6884;
}
.game-intro__wrapper--single:hover {
  background-color: #384e63;
}
.game-intro__wrapper--multiplay {
  background-color: #3a98d4;
}
.game-intro__wrapper--multiplay:hover {
  background-color: #277db4;
}
.game-intro__content {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 200px;
  height: 200px;
  margin-top: -100px;
  margin-left: -100px;
  line-height: 200px;
  font-size: 1em;
  text-align: center;
  text-transform: uppercase;
}
.game-intro__content--large {
  font-size: 6em;
}

.game-board {
  width: 580px;
  height: 690px;
  margin: 5% auto;
  padding: 5px;
  background-color: #fff;
  box-shadow: 0px 0px 25px 0px rgba(58, 152, 212, 0.5);
}
.game-board__heading {
  clear: both;
  height: 90px;
  margin-top: 20px;
}
.game-board__score {
  float: left;
  width: calc(100% / 3);
  text-align: center;
  font-weight: bold;
}
.game-board__score--player-one-win {
  color: #3a98d4;
}
.game-board__score--player-two-win {
  color: #4a6884;
}
.game-board__score--draw {
  color: #666;
}
.game-board__score-name {
  margin-top: 5px;
}
.game-board__score-total {
  display: block;
  font-size: 1em;
}
.game-board__body {
  clear: both;
  width: 538px;
  height: 550px;
  margin: 10px auto;
  position: relative;
}
.tic-tac-toe {
  margin: 0 auto;
}
.tic-tac-toe .row {
  float: left;
}
.tic-tac-toe .row:first-child .square {
  border-top: 0;
}
.tic-tac-toe .square {
  float: left;
  width: 0.7em;
  height: 0.7em;
  cursor: default;
  color: #4a6884;
  border-color: #ccc;
  font-size: 6em;
  line-height: 0.73em;
  border-style: solid;
  border-width: 2px 2px 0 0;
  text-align: center;
}

.tic-tac-toe .square .square-content {
  display: default;
  font-size: 0.6em;
  margin-block-start: 0em;
  margin-block-end: 0em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}
.tic-tac-toe .square:last-child {
  border-right: 0px;
}
.tic-tac-toe .x-marker {
  color: #4a6884;
}
.tic-tac-toe .o-marker {
  color: #3a98d4;
}

.game-board__result {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.775);
  display: flex;
  padding: 26% 0 0 0;
  align-items: center;
  flex-direction: column;
}

.message {
  text-align: center;
  font-family: "Avant Garde", Avantgarde, "Century Gothic", CenturyGothic,
    "AppleGothic", sans-serif;
  font-size: 30px;
  text-align: center;
  text-transform: uppercase;
  text-rendering: optimizeLegibility;
  color: #131313;
  letter-spacing: 0.15em;
}

button {
  margin: 20px;
}
.custom-btn {
  width: 130px;
  height: 40px;
  color: #fff;
  border-radius: 5px;
  padding: 10px 25px;
  font-family: "Lato", sans-serif;
  font-weight: 500;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: inline-block;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, 0.5),
    7px 7px 20px 0px rgba(0, 0, 0, 0.1), 4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  outline: none;
  margin: 0px;
}
.btn-3 {
  background: rgb(0, 172, 238);
  background: linear-gradient(
    0deg,
    rgba(0, 172, 238, 1) 0%,
    rgba(2, 126, 251, 1) 100%
  );
  width: 130px;
  height: 40px;
  line-height: 42px;
  padding: 0;
  border: none;
}
.btn-3 span {
  position: relative;
  display: block;
  width: 100%;
  height: 100%;
}
.btn-3:before,
.btn-3:after {
  position: absolute;
  content: "";
  right: 0;
  top: 0;
  background: rgba(2, 126, 251, 1);
  transition: all 0.3s ease;
}
.btn-3:before {
  height: 0%;
  width: 2px;
}
.btn-3:after {
  width: 0%;
  height: 2px;
}
.btn-3:hover {
  background: transparent;
  box-shadow: none;
}
.btn-3:hover:before {
  height: 100%;
}
.btn-3:hover:after {
  width: 100%;
}
.btn-3 span:hover {
  color: rgba(2, 126, 251, 1);
}
.btn-3 span:before,
.btn-3 span:after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  background: rgba(2, 126, 251, 1);
  transition: all 0.3s ease;
}
.btn-3 span:before {
  width: 2px;
  height: 0%;
}
.btn-3 span:after {
  width: 0%;
  height: 2px;
}
.btn-3 span:hover:before {
  height: 100%;
}
.btn-3 span:hover:after {
  width: 100%;
}

.stage {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 10px 0;
  /* margin: 0 -5%; */
  overflow: hidden;
}

.dot-typing {
  position: relative;
  left: -9999px;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background-color: #41c4de;
  color: #41c4de;
  box-shadow: 9984px 0 0 0 #41c4de, 9999px 0 0 0 #41c4de, 10014px 0 0 0 #41c4de;
  animation: dot-typing 1.5s infinite linear;
}

@keyframes dot-typing {
  0% {
    box-shadow: 9984px 0 0 0 #41c4de, 9999px 0 0 0 #41c4de,
      10014px 0 0 0 #41c4de;
  }
  16.667% {
    box-shadow: 9984px -10px 0 0 #41c4de, 9999px 0 0 0 #41c4de,
      10014px 0 0 0 #41c4de;
  }
  33.333% {
    box-shadow: 9984px 0 0 0 #41c4de, 9999px 0 0 0 #41c4de,
      10014px 0 0 0 #41c4de;
  }
  50% {
    box-shadow: 9984px 0 0 0 #41c4de, 9999px -10px 0 0 #41c4de,
      10014px 0 0 0 #41c4de;
  }
  66.667% {
    box-shadow: 9984px 0 0 0 #41c4de, 9999px 0 0 0 #41c4de,
      10014px 0 0 0 #41c4de;
  }
  83.333% {
    box-shadow: 9984px 0 0 0 #41c4de, 9999px 0 0 0 #41c4de,
      10014px -10px 0 0 #41c4de;
  }
  100% {
    box-shadow: 9984px 0 0 0 #41c4de, 9999px 0 0 0 #41c4de,
      10014px 0 0 0 #41c4de;
  }
}

.dot-spin {
  position: relative;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  background-color: transparent;
  color: transparent;
  box-shadow: 0 -18px 0 0 #9880ff, 12.727926px -12.727926px 0 0 #9880ff,
    18px 0 0 0 #9880ff, 12.727926px 12.727926px 0 0 rgba(152, 128, 255, 0),
    0 18px 0 0 rgba(152, 128, 255, 0),
    -12.727926px 12.727926px 0 0 rgba(152, 128, 255, 0),
    -18px 0 0 0 rgba(152, 128, 255, 0),
    -12.727926px -12.727926px 0 0 rgba(152, 128, 255, 0);
  animation: dot-spin 1.5s infinite linear;
}

.game-board__wait-scene {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.775);
  display: flex;
  /* padding: 26% 0 0 0; */

  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.game-board__wait-scene .message {
  animation: zoom-shrink 2s infinite;
}

@keyframes zoom-shrink {
  0% {
    transform: scale(1); /* Initial size */
  }
  50% {
    transform: scale(0.8); /* Zoom out */
  }
  100% {
    transform: scale(1); /* Shrink */
  }
}
.tic-tac-toe {
  width: 100%;
  height: 100%;
}
.tic-tac-toe .row {
  height: calc(100% / 8 - 5px);
  width: 100%;
}
.tic-tac-toe .square {
  width: calc(100% / 8);
}

@media screen and (max-width: 768px) {
  .game-board {
    width: 400px;
    height: 510px;
  }
  .game-board__body {
    width: 390px;
    height: 390px;
    overflow: hidden;
  }
  .tic-tac-toe .square {
    line-height: 0.45em;
  }
  .tic-tac-toe .square .square-content {
    font-size: 0.5em;
  }
}

@media screen and (max-width: 420px) {
  .game-board {
    width: 350px;
    height: 460px;
  }
  .game-board__body {
    width: 300px;
    height: 300px;
  }
  .tic-tac-toe .row {
    height: calc(100% / 8 + 1px);
    width: 100%;
  }
  .tic-tac-toe .square {
    line-height: 0.4em;
  }
  .game-board__score-total {
    display: block;
    font-size: 0.8em;
  }
}
@media screen and (max-width: 350px) {
  .game-board {
    width: 300px;
    height: 430px;
  }
  .game-board__body {
    width: 280px;
    height: 280px;
  }
  .tic-tac-toe .row {
    height: calc(100% / 8);
    width: 100%;
  }
  .tic-tac-toe .square {
    line-height: 0.36em;
  }
}
