h1 {
  font-size: 3em;
  font-weight: 300;
  line-height: 1em;
  text-align: center;
  color: #4dc3fa;
}

h2 {
  font-size: 1em;
  font-weight: 300;
  text-align: center;
  display: block;
  line-height: 1em;
  padding-bottom: 2em;
  color: #fb667a;
}

h2 a {
  font-weight: 700;
  text-transform: uppercase;
  color: #fb667a;
  text-decoration: none;
}

.blue {
  color: #185875;
}
.yellow {
  color: #fff842;
}

.container th h1 {
  font-weight: bold;
  font-size: 1em;
  text-align: left;
  color: #185875;
}

.container td {
  font-weight: normal;
  font-size: 1em;
  -webkit-box-shadow: 0 2px 2px -2px #0e1119;
  -moz-box-shadow: 0 2px 2px -2px #0e1119;
  box-shadow: 0 2px 2px -2px #0e1119;
}

.container {
  text-align: left;
  overflow: hidden;
  width: 80%;
  margin: 0 auto;
  display: table;
  padding: 0 0 8em 0;
}

.container td,
.container th {
  padding-bottom: 1%;
  padding-top: 1%;
  padding-left: 1%;
  width: calc(100% / 4);
}

/* Background-color of the odd rows */
.container tr:nth-child(odd) {
  background-color: white;
}

/* Background-color of the even rows */
.container tr:nth-child(even) {
  background-color: white;
}

.container th {
  background-color: #c9dff0;
}

.container td:first-child {
  color: black;
}

.container tr:hover {
  background-color: rgb(248, 223, 223);
  -webkit-box-shadow: 0 6px 6px -6px #0e1119;
  -moz-box-shadow: 0 6px 6px -6px #0e1119;
  box-shadow: 0 6px 6px -6px #0e1119;
}

.container td:hover {
  background-color: #97f5e2;
  color: #403e10;
  font-weight: bold;

  box-shadow: #7f7c21 -1px 1px, #7f7c21 -2px 2px, #7f7c21 -3px 3px,
    #7f7c21 -4px 4px, #7f7c21 -5px 5px, #7f7c21 -6px 6px;
  transform: translate3d(6px, -6px, 0);

  transition-delay: 0s;
  transition-duration: 0.4s;
  transition-property: all;
  transition-timing-function: line;
}

.lobby-button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #4caf50;
  width: 120px;
  border: 0;
  padding: 10px 10px;
  color: #ffffff;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.lobby-button:hover,
.lobby-button:active,
.lobby-button:focus {
  background: #43a047;
}

.search-container {
  display: flex;
  align-items: center;
}

.search-container button {
  float: right;
  padding: 6px 10px;
  /* margin: 8px 0 0 0; */
  /* margin-right: 16px; */
  margin: 0;
  background: #ddd;
  font-size: 17px;
  border: none;
  cursor: pointer;
}

.search-container form {
  display: flex;
  align-items: center;
}

.button-join-room {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #4caf50;
  width: 120px;
  border: 0;
  padding: 10px 10px;
  color: #ffffff;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
  margin: 0px;
}
.button-join-room:hover,
.button-join-room:active,
.button-join-room:focus {
  background: grey;
}

@media screen and (max-width: 560px) {
  .container {
    font-size: 13px;
  }
  .lobby-button {
    width: 80px;
    font-size: 10px;
    padding: 10px;
  }
}

@media screen and (max-width: 380px) {
  .container {
    font-size: 11px;
  }
}
