@import url("https://fonts.googleapis.com/css?family=Montserrat:100,400&display=swap");
@keyframes kahoot {
  25% {
    background: #41c4de;
  }
  50% {
    background: #8cc73f;
  }
  75% {
    background: #f3d333;
  }
  100% {
    background: #f78e56;
  }
}
.login-container {
  background: #42c4de;
  animation: kahoot 14s infinite;
  animation-direction: alternate;
  font-family: "Montserrat", sans-serif;
}
.top input {
  width: 238px;
  height: 50px;
  border: 2px #ccd4d0 solid;
  margin-left: auto;
  margin-right: auto;
  display: block;
  transition: 0.3s;
  padding-top: 4px;
  padding-right: 6px;
  padding-bottom: 4px;
  padding-left: 6px;
}
.top button {
  width: 238px;
  height: 50px;
  border: 0px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  transition: 0.3s;
  background-color: #333333;
  color: white;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  font-weight: 700;
  border-radius: 3px;
  cursor: pointer;
  text-align: center;
}
.top input:focus {
  border: 2px black solid;
}
.top img {
  width: 255px;
  height: 89.9851px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  filter: invert(99%) sepia(95%) saturate(0%) hue-rotate(33deg) brightness(104%)
    contrast(101%);
}
.top input[type="text"] {
  font-size: 1.25rem;
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  color: black;
}
.top input::-webkit-input-placeholder {
  font-family: "Montserrat", sans-serif;
  color: #b5b5b5;
}

.top input:-ms-input-placeholder {
  font-family: "Montserrat", sans-serif;
  color: #b5b5b5;
}

.top input:-moz-placeholder {
  font-family: "Montserrat", sans-serif;
  color: #b5b5b5;
}

.top input::-moz-placeholder {
  font-family: "Montserrat", sans-serif;
  color: #b5b5b5;
}
.top input + button {
  margin-top: 8px;
}

.top {
  text-align: center;
}
